.App-logo{
    display: block;
    height:300px;
    width: 200px;
}

.card1{
    position: relative; 
    border: 1px solid #d5d5d5;
    border-radius: 4px;
    width: 200px;
    height: 400px;
    background-color: #f1f1f1;
    margin: 10px;
    padding: 10px
}

/* >>> TESTE <<< */

.overlay{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #008CBA;
    overflow: hidden;
    width: 0;
    height: 100%;
    transition: .5s ease;

    opacity:0.9;

}

.card1:hover .overlay {
    width: 100%;
  }

.text{
    white-space: pre-line; 
    color: rgb(255, 255, 255);
    margin-right: -70px;
    margin-left: 110px;
    font-size: 18px;
    position: absolute;
    overflow: hidden;
    top: 50%;
    /* left: 50%; */
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}

.info{
    margin-top: 15px;
}

